@import url('https://fonts.googleapis.com/css2?family=Archivo+Black&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

.font-cormorant {
  font-family: 'Cormorant Garamond', serif;
  font-weight: 300;
}

.font-raleway {
  font-family: 'Raleway', sans-serif;
}

#gratitude-garland-root {
  font-family: 'Raleway', sans-serif;
}

.transition-colors {
  transition: background-color 0.3s ease, color 0.3s ease;
}

button, .cursor-pointer {
  cursor: pointer;
}

input:focus {
  outline: 2px solid #22C55E;
  outline-offset: -2px;
}


/* Hide Flodesk form until needed */ 
[data-flodesk-embed] {
  display: none;
}

#garland-app { 
  color: rgba(68, 68, 68, 1);
  line-height: 2;
  letter-spacing: 0.05em;
  font-size: 14px;
  text-align: left;
  font-family: 'Raleway';
  font-weight: 400;
  font-style: normal;
}

/* Form Section */
#form-section,
#match-section {
  background-color: #f5e8e3;
}

#form-section button,
#match-section button {
  color: #ffffff;
  border: 1px solid #e39f40;
  display: inline-block;
  padding: 17px 28px;
  font-size: 12px;
  background: #E39F40;
  text-align: center;
  font-family: benton-sans, sans-serif;
  font-weight: 600;
  line-height: 20px;
  white-space: nowrap;
  border-radius: 5.6000000000000005px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  transition: opacity 0.3s ease;
}
#form-section button:hover {
  opacity: 0.86;
}

#form-section input,
#match-section input {
  color: #E39F40 !important;
  border: 1px solid #E39F40 !important;
  font-size: 12px;
  background: #ffffff;
  font-family: benton-sans, sans-serif;
  font-weight: 600;
  text-transform: capitalize;
  height: 56px;
  line-height: 22px;
}
#form-section input.linkedin,
#form-section input.email {
  text-transform: none !important;
}
#form-section input:focus,
#match-section input:focus {
  outline: 2px solid #f0910c;
}

/* Match Section */
#match-section h3 {
  font-weight: 600;
  font-size: 1rem;
}

.best-match p {
  font-size: 1rem;
  font-style: italic;
  display: inline-block;
  float: right;
  /* padding: 5px 10px; */
  /* background-color: rgba(222, 135, 111, 0.8); */
  /* color: white !important; */
}

/* HELPERS */
.flex.items-start.gap-2 {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
}

input#emailConsent {
  outline: 0px solid #f0910c !important;
}

.text-3xl.font-cormorant.text-green-800,
.text-2xl.font-cormorant.text-green-800 {
  color: rgb(123, 73, 2) !important;
}

.font-medium.text-green-700,
.text-green-700 {
  color: rgb(212, 126, 6) !important;
}
.text-green-600.ml-2 {
  color: rgba(227, 159, 64, 1) !important;
}

/* Linkedin */
a.linkedin-link {
  color: #0a66c2 !important;
  font-size: 0.7rem;
}
img.image-linkedin {
  height: 20px;
  display: inline-block;
  position: relative;
  top: -2px;
}

form p.text-sm {
  padding-top: 2px;
}
